<template>
  <div class="main-container">
    <div class="tool-header">
      <h1>ğŸ§® 21ç‚¹è®¡ç®—å™¨</h1>
      <p>é€‰æ‹©4ä¸ª1-10çš„æ•°å­—ï¼Œä½¿ç”¨åŠ ã€å‡ã€ä¹˜ã€é™¤è¿ç®—ï¼Œæ±‚å‡ºç›®æ ‡ç‚¹æ•°</p>
    </div>

    <el-row :gutter="22">
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="input-card">
          <div slot="header">
            <span>æ¸¸æˆè®¾ç½®</span>
            <div style="float: right;">
              <el-button size="mini" @click="resetNumbers">éšæœºç”Ÿæˆ</el-button>
            </div>
          </div>

          <el-form label-position="top">
            <el-form-item label="ç›®æ ‡ç‚¹æ•° (Target)">
              <el-select v-model="targetValue" placeholder="é€‰æ‹©ç›®æ ‡ç‚¹æ•°" style="width: 100%;">
                <el-option label="24 (ç»å…¸24ç‚¹)" :value="24"></el-option>
                <el-option label="21" :value="21"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="é€‰æ‹© 4 ä¸ªæ•°å­— (1-10)">
              <div class="number-input-grid">
                <el-select
                    v-for="(_, index) in numbers"
                    :key="index"
                    v-model="numbers[index]"
                    placeholder="é€‰æ‹©æ•°å­—"
                    size="medium"
                    class="puzzle-select"
                    filterable
                >
                  <el-option
                      v-for="n in 10"
                      :key="n"
                      :label="String(n)"
                      :value="n"
                  ></el-option>
                </el-select>
              </div>
            </el-form-item>
          </el-form>

          <el-button
              type="primary"
              @click="solvePuzzle"
              :loading="loading"
              style="width: 100%; margin-top: 10px;"
          >
            {{ loading ? 'è®¡ç®—ä¸­...' : 'å¼€å§‹æ±‚è§£' }}
          </el-button>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="result-card">
          <div slot="header">
            <span>æ±‚è§£ç»“æœ ({{ solutions.length }} ä¸ª)</span>
          </div>

          <div v-if="solutions.length > 0" class="solution-list">
            <el-alert
                :title="`æˆåŠŸæ‰¾åˆ° ${solutions.length} ä¸ªè§£æ³•`"
                type="success"
                show-icon
                :closable="false"
                style="margin-bottom: 15px;"
            ></el-alert>
            <el-collapse v-model="activeNames" accordion>
              <el-collapse-item
                  v-for="(sol, index) in solutions"
                  :key="index"
                  :title="`è§£æ³• #${index + 1}`"
                  :name="index"
              >
                <div class="solution-item">{{ sol }}</div>
              </el-collapse-item>
            </el-collapse>
          </div>

          <div v-else-if="loading" class="no-result">
            <i class="el-icon-loading"></i>
            <p>æ­£åœ¨åŠªåŠ›è®¡ç®—ä¸­...</p>
          </div>

          <div v-else class="no-result">
            <i class="el-icon-search"></i>
            <p v-if="hasAttempted">æŠ±æ­‰ï¼Œæœªæ‰¾åˆ°ä½¿ç”¨è¿™ 4 ä¸ªæ•°å­—è¿ç®—å‡º {{ targetValue }} çš„è§£æ³•ã€‚</p>
            <p v-else>è¯·é€‰æ‹© 4 ä¸ªæ•°å­—å’Œç›®æ ‡ç‚¹æ•°ï¼Œå¼€å§‹æ±‚è§£ã€‚</p>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'BlackjackView',
  metaInfo: {
    title: '21ç‚¹è®¡ç®—å·¥å…·',
    meta: [
      { name: 'description', content: 'å››åˆ™è¿ç®—æ±‚è§£å™¨ï¼Œå¸®åŠ©ä½ æ‰¾åˆ°4ä¸ªæ•°å­—é€šè¿‡åŠ å‡ä¹˜é™¤è®¡ç®—å‡º24æˆ–21ç­‰ç›®æ ‡ç‚¹æ•°çš„å…¨éƒ¨è§£æ³•ã€‚çº¯æ•°å­¦ç›Šæ™ºå·¥å…·ã€‚' },
      { name: 'keywords', content: '24ç‚¹è®¡ç®—å™¨,21ç‚¹æ•°å­¦,å››åˆ™è¿ç®—,ç›Šæ™ºæ¸¸æˆ,æ•°å­—æ±‚è§£å™¨,æ•°å­¦è°œé¢˜' }
    ]
  },
  data() {
    return {
      numbers: [1, 1, 1, 1],
      targetValue: 21,
      solutions: [],
      loading: false,
      hasAttempted: false, // æ ‡è®°æ˜¯å¦è¿›è¡Œè¿‡æ±‚è§£å°è¯•
      activeNames: [0]
    }
  },
  mounted() {
    this.resetNumbers();
  },
  methods: {
    // éšæœºç”Ÿæˆ 4 ä¸ª 1-10 ä¹‹é—´çš„æ•°å­—
    resetNumbers() {
      this.numbers = this.numbers.map(() => Math.floor(Math.random() * 10) + 1);
      this.solutions = [];
      this.hasAttempted = false;
    },

    // æ ¸å¿ƒæ±‚è§£é€»è¾‘ï¼šä½¿ç”¨é€’å½’å’Œå…¨æ’åˆ—æ¥æ‰¾åˆ°æ‰€æœ‰è§£æ³•
    solvePuzzle() {
      if (this.numbers.some(num => !num || num < 1 || num > 10)) {
        this.$message.error('è¯·ç¡®ä¿æ‰€æœ‰æ•°å­—éƒ½åœ¨ 1 åˆ° 10 ä¹‹é—´');
        return;
      }

      this.loading = true;
      this.solutions = [];
      this.hasAttempted = true;

      // å»¶è¿Ÿæ‰§è¡Œï¼Œé˜²æ­¢é˜»å¡ UI
      setTimeout(() => {
        const uniqueSolutions = new Set();
        const operators = ['+', '-', '*', '/'];
        const target = this.targetValue;

        // 1. ç”Ÿæˆæ•°å­—çš„å…¨æ’åˆ— (Permutations)
        const permutations = this.getPermutations(this.numbers);

        // 2. éå†æ‰€æœ‰æ’åˆ—ã€æ“ä½œç¬¦å’Œæ‹¬å·ç»„åˆ
        for (const nums of permutations) {
          this.findSolutions(nums, operators, target, uniqueSolutions);
        }

        this.solutions = Array.from(uniqueSolutions);
        this.loading = false;

        if (this.solutions.length > 0) {
          this.$message.success(`æ‰¾åˆ° ${this.solutions.length} ä¸ªè§£æ³•ï¼`);
        } else {
          this.$message.info(`æœªæ‰¾åˆ°è§£æ³•ã€‚`);
        }
      }, 50);
    },

    // --- æ±‚è§£å†…éƒ¨å·¥å…·å‡½æ•° ---

    // é€’å½’è·å–æ•°å­—çš„å…¨æ’åˆ—
    getPermutations(arr) {
      if (arr.length === 1) return [arr];

      const result = new Set();
      for (let i = 0; i < arr.length; i++) {
        const first = arr[i];
        const rest = [...arr.slice(0, i), ...arr.slice(i + 1)];
        const permsOfRest = this.getPermutations(rest);

        for (const perm of permsOfRest) {
          result.add([first, ...perm].join(','));
        }
      }
      return Array.from(result).map(str => str.split(',').map(Number));
    },

    // æ ¸å¿ƒé€’å½’æŸ¥æ‰¾è§£æ³•
    findSolutions(nums, operators, target, uniqueSolutions) {
      if (nums.length === 1) {
        // æ£€æŸ¥æœ€ç»ˆç»“æœæ˜¯å¦ç­‰äºç›®æ ‡å€¼ (ä½¿ç”¨æµ®ç‚¹æ•°å®¹å·®)
        if (Math.abs(nums[0] - target) < 1e-6) {
          // æ‰¾åˆ°è§£æ³• (ä½†æˆ‘ä»¬æ²¡æœ‰è®°å½•è¿ç®—è¿‡ç¨‹ï¼Œä¸‹é¢æˆ‘ä»¬ä¼˜åŒ–ä¸€ä¸‹)
          // âš ï¸ æ³¨æ„ï¼šæ­¤å¤„ä»…ä½œç®€åŒ–ï¼Œå®é™…éœ€è¦é€’å½’è®°å½•è¡¨è¾¾å¼
        }
        return;
      }

      // è¿™æ˜¯ä¸€ä¸ªéå¸¸ç®€åŒ–çš„è§£æ³•ï¼Œå®Œæ•´çš„è§£æ³•éœ€è¦å¤æ‚çš„è¡¨è¾¾å¼è§£ææ ‘
      // é‰´äºè¿™æ˜¯ä¸€ä¸ª UI ç»„ä»¶ï¼Œæˆ‘ä»¬é‡‡ç”¨ä¸€ä¸ªæ›´ç®€å•ä½†èƒ½æ‰¾åˆ°å¤§éƒ¨åˆ†è§£çš„è¿­ä»£æ–¹æ³•ï¼š
      const N = nums.length;
      if (N === 4) {
        // ç©·ä¸¾ 4 ä¸ªæ•°å­—çš„è¿ç®—é¡ºåº
        this.generateExpressions(nums, operators, target, uniqueSolutions);
      }
    },

    // ç®€åŒ–ç‰ˆï¼šç”Ÿæˆå¸¸è§çš„äº”ç§è¡¨è¾¾å¼ç»„åˆ
    generateExpressions(nums, operators, target, uniqueSolutions) {
      // nums å·²ç»æ˜¯å…¨æ’åˆ—çš„å››ä¸ªæ•°å­— [a, b, c, d]

      // ç¡®ä¿æ•°ç»„å·²æ’åºï¼Œä»¥è¿›è¡Œè§£æ³•å»é‡
      //const sortedNums = [...nums].sort((a, b) => a - b);

      // éå†æ‰€æœ‰æ“ä½œç¬¦ç»„åˆ O1, O2, O3 (4*4*4 = 64 ç§)
      for (const o1 of operators) {
        for (const o2 of operators) {
          for (const o3 of operators) {

            //const ops = [o1, o2, o3];

            // 1. (a O1 b) O2 (c O3 d)
            this.evaluateAndRecord(
                `(${nums[0]} ${o1} ${nums[1]}) ${o2} (${nums[2]} ${o3} ${nums[3]})`,
                target, uniqueSolutions
            );

            // 2. ((a O1 b) O2 c) O3 d
            this.evaluateAndRecord(
                `(((${nums[0]} ${o1} ${nums[1]}) ${o2} ${nums[2]}) ${o3} ${nums[3]})`,
                target, uniqueSolutions
            );

            // 3. (a O1 (b O2 c)) O3 d
            this.evaluateAndRecord(
                `(${nums[0]} ${o1} (${nums[1]} ${o2} ${nums[2]})) ${o3} ${nums[3]}`,
                target, uniqueSolutions
            );

            // 4. a O1 ((b O2 c) O3 d)
            this.evaluateAndRecord(
                `${nums[0]} ${o1} ((${nums[1]} ${o2} ${nums[2]}) ${o3} ${nums[3]})`,
                target, uniqueSolutions
            );

            // 5. a O1 (b O2 (c O3 d))
            this.evaluateAndRecord(
                `${nums[0]} ${o1} (${nums[1]} ${o2} (${nums[2]} ${o3} ${nums[3]}))`,
                target, uniqueSolutions
            );
          }
        }
      }
    },

    // è¯„ä¼°è¡¨è¾¾å¼å¹¶è®°å½•ç»“æœ
    evaluateAndRecord(expression, target, uniqueSolutions) {
      try {
        // ä½¿ç”¨ eval å¿«é€Ÿè®¡ç®—è¡¨è¾¾å¼ï¼Œä½†å¿…é¡»æ³¨æ„é™¤æ³• (ä¸èƒ½é™¤ä»¥ 0 ä¸”ç»“æœå¿…é¡»æ˜¯æ•´æ•°æˆ–éå¸¸æ¥è¿‘æ•´æ•°)
        // ä½¿ç”¨ Function æ„é€ å‡½æ•°æ›¿ä»£ evalï¼Œæ›´å®‰å…¨ä¸€äº›ï¼Œä½†ä»ç„¶éœ€è¦æ³¨æ„å®‰å…¨å’Œæ€§èƒ½
        const result = Function(`'use strict'; return ${expression}`)();

        if (Math.abs(result - target) < 1e-6) {
          // ä»…æ¥å—æ•´æ•°ç»“æœï¼ˆæˆ–éå¸¸æ¥è¿‘æ•´æ•°çš„æµ®ç‚¹æ•°ï¼Œå¦‚ 24.0000000001ï¼‰
          if (Math.abs(result - Math.round(result)) < 1e-6) {
            uniqueSolutions.add(expression);
          }
        }
      } catch (e) {
        // æ•è·é™¤ä»¥é›¶æˆ–è¯­æ³•é”™è¯¯
      }
    }
  }
}
</script>

